import { Types } from "../types/types";

export const RegisterAction = (Usuario, contrasea) =>{
    return{
        type: Types.registro,
        payload: {
            Usuario,
            contrasea,
            logged:true
        }
    }
}
export const LogoutAction = ()=>{
    return{
        type:Types.logout,
        payload:{
            logged:false
            
        }
        
    }
}